import React, { FC } from "react";

import { uuid } from "../utils";

import Card from "./Card";
import { HorizontalCardProps } from "./Card.types";
import { StyledHorizontalCards } from "./SegmentedHorizontalCards.styles";


const SegmentedHorizontalCards: FC<HorizontalCardProps> = ({
	ariaLabel,
	dataQA,
	className,
	mt,
	mb,
	ml,
	mr,
	m,
	cards,
}) => (
	<StyledHorizontalCards
		aria-label={ariaLabel}
		data-qa={dataQA}
		className={className}
		mt={mt}
		mb={mb}
		mr={mr}
		ml={ml}
		m={m}
	>
		{cards.map((props) => (
			<Card key={uuid()} dataQA={props.dataQA} ariaLabel={props.ariaLabel} isInverted={false}>
				{props.children}
			</Card>
		))}
	</StyledHorizontalCards>
);

SegmentedHorizontalCards.displayName = "SegmentedHorizontalCards";
export default SegmentedHorizontalCards;
